* {
  margin: 0;
  padding: 0;
}
html {
  box-sizing: border-box;
  --bgColorMenu: #1d1d27;
  --duration: 0.7s;
}

html *,
html *::before,
html *::after {
  box-sizing: inherit;
}

body {
  /* overflow: hidden; */
  /* height: 100vh; */
}
.wrapper-outer {
  margin: 0;
  display: flex;
  height: 100vh;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  /* background: linear-gradient(#c6ffdd, #fbd786, #f7797d); */
  -webkit-tap-highlight-color: transparent;
  transition: background-color 0.7s;
  transition: background-color var(--duration);
}
.aside {
  width: 10%;
}
.wrapper_inner {
  width: 90%;
  height: 100vh;
}

.content {
  /* width: 50%; */

  margin: 0 auto;
  height: 100%;
  border-radius: 16px;
  /* overflow-y: auto; */
}

.menu {
  margin: 0;
  display: flex;
  height: 100%;
  overflow: hidden;
  /* Works well with 100% width  */
  width: 100%;
  font-size: 1em;
  padding: 0 30px;
  flex-direction: column;
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: #1d1d27;
  background-color: var(--bgColorMenu);
}

.menu__item {
  all: unset;
  flex-grow: 1;
  width: 35px;
  z-index: 100;
  display: flex;
  cursor: pointer;
  position: relative;
  border-radius: 50%;
  align-items: center;
  will-change: transform;
  justify-content: center;
  padding: 0.55em 0 0.85em;
  transition: transform var(--duration);
  transition: transform var(--timeOut, var(--duration));
}

.menu__item::before {
  content: "";
  z-index: -1;
  width: 4.2em;
  height: 4.2em;
  border-radius: 50%;
  position: absolute;
  transform: scale(0);
  transition: background-color 0.7s, transform 0.7s;
  transition: background-color var(--duration), transform var(--duration);
}
.menu__item.active {
  transform: translate3d(0, -0.8em, 0);
}
.menu__item.active::before {
  transform: scale(1);
  background-color: var(--bgColorItem);
}
.icon {
  width: 2.4em;
  height: 2.4em;
  stroke: white;
  fill: transparent;
  stroke-width: 1pt;
  stroke-miterlimit: 10;
  stroke-linecap: round;
  font-size: 16px;
  stroke-linejoin: round;
  stroke-dasharray: 400;
}

.menu__item.active .icon {
  animation: strok 1.5s reverse;
}

@keyframes strok {
  100% {
    stroke-dashoffset: 400;
  }
}
.menu__border {
  border-radius: 547px;
  display: none;
  left: 0;
  z-index: 99;
  bottom: -2%;
  width: 5.9em;
  height: 5.9em;
  position: absolute;
  -webkit-clip-path: url(#menu);
          clip-path: url(#menu);
  will-change: transform;
  background-color: #6b1a92;
  transition: transform var(--duration);
  transition: transform var(--timeOut, var(--duration));
}
